h1, h2, h3, h4, h5, h6, p, td, li, a {
	font-family: "Rubik";
}

h1 {
	font-size: 40px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 25px;
}

p {
	font-size: 16px;
}

.teaser__text {
	font-size: 16px;
	font-weight: 400;
	line-height: 28px;
}

.contact__name {
	font-size: 11px;
	font-weight: 500;
	line-height: 13px;
}

.contact__status {
	font-size: 8px;
	font-weight: 400;
	line-height: 9px;
}

.message__text {
	font-size: 8px;
	font-weight: 400px;
	line-height: 11px;
}

.message__price {
	font-size: 12px;
	font-weight: 700;
	line-height: 11px;
}

.input__placeholder {
	font-size: 9px;
	font-weight: 400;
	line-height: 11px;
}
.icon {
	display: inline-block;
	font: normal normal normal 1em/1 'Nucleo';
	color: inherit;
	flex-shrink: 0;
	text-transform: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	&--sm { font-size: 12px; }
	&--md { font-size: 16px; }
	&--lg { font-size: 20px; }

}

.icon-arrow-left::before { content: '/ea02'; }
.icon-arrow-right::before { content: '/ea03'; }
.icon-menu::before { content: '/ea04'; }

$white: #FFFFFF;
$lightgrey: #FAFAFA;
$grey: #A39DA9;
$black: #6E5D7E;
$magenta: #E844FF;
$violet: #8739FF;
$gradient: linear-gradient(225deg, $magenta 0%, $magenta 0.01%, $violet 100%);

* {
	box-sizing: border-box;
}